<template>
  <side-menu></side-menu>
  <div class="content-wrapper">
    <div class="content-header">
      <h1></h1>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> </a></li>
        <li><a href="#"></a></li>
        <li class="active"></li>
      </ol>
    </div>
    <div class="content">
      <div class="mb10">
        <button class="btn btn-primary" @click="addMarket"></button>
        <button class="btn btn-primary" @click="addFloor"></button>
        <a class="btn btn-danger" href="/market" target="_blank"></a>
      </div>
      <div class="row">
        <div class="col-xs-7">
          <div class="box">
            <div class="box-header">
              <h3 class="box-title"></h3>
            </div>
            <div class="box-body table-responsive no-padding">
              <table class="table table-hover">
                <tr>
                  <th width="50%"></th>
                  <th></th>
                  <!-- <th></th> -->
                  <th>
                    <dropdown>
                      <a href="javascript:;" data-toggle="dropdown">
                        
                        <span class="caret"></span>
                      </a>
                      <ul slot="dropdown-menu" class="dropdown-menu dropdown-area">
                        <li @click="floorFilter(null)"><a href="javascript:;"></a></li>
                        <template v-for="item in floors">
                          <li @click="floorFilter(item._id)"><a href="javascript:;">{{item.name}}</a></li>
                        </template>
                      </ul>
                    </dropdown>
                  </th>
                  <th></th>
                </tr>
                <tr v-for="item in markets">
                  <td>
                    <div class="media">
                      <div class="media-left">
                        <img v-bind:src="item.img | resize" width="100px" alt="">
                      </div>
                      <div class="media-body">
                        <h4>{{ item.name }}</h4>
                        <p>{{ item.description }}</p>
                      </div>
                    </div>
                  </td>
                  <td>
                    {{ item.ishidden | ishidden }}
                  </td>
                  <td>
                    {{ item.floorId.name }}
                  </td>
                  <!-- <td>
                    {{ item.hits }}
                  </td> -->
                  <td>
                    <a class="btn btn-primary" v-link="'/market/' + item._id"></a>
                    <!-- <button class="btn btn-primary" @click="editMarket($index)"></button> -->
                    <button class="btn btn-default" @click="delMarket(item._id, $index)"></button>
                  </td>
                </tr>
              </table>
              <div class="col-md-6">
                <!-- {{ page.total }} {{ page.perPage }} -->
              </div>
              <div class="col-md-6">
                <div class="text-right">
                  <pages :total.sync="page.total" :current="page.current" :per-page="page.perPage" :page-changed="pagerCallback"></pages>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xs-5">
          <div class="box">
            <div class="box-header">
              <h3 class="box-title"></h3>
            </div>
            <div class="box-body table-responsive no-padding">
              <table class="table table-hover">
                <tr>
                  <th width="50%"></th>
                  <th></th>
                  <th></th>
                </tr>
                <tr v-for="item in floors">
                  <td>
                    {{ item.name }}
                  </td>
                  <td>
                    {{ item.ishidden | ishidden }}
                  </td>
                  <td>
                    <!-- <a class="btn btn-primary" v-link="'/market/' + item._id"></a> -->
                    <button class="btn btn-primary" @click="editFloor($index)"></button>
                    <button class="btn btn-default" @click="delFloor(item._id, $index)"></button>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!---->
  <modal :show.sync="showModal" title="">
    <div slot="modal-body" class="modal-body">
      <form class="form-horizontal" v-ajax-submit @submit.prevent="saveMarket">
        <div class="form-horizontal">
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="market.name" required>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="market.title">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <upload id="pick2" text="" :img.sync="market.img"></upload>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label">logo</label>
            <div class="col-sm-10">
              <upload id="pick-marketlogo" text="" :img.sync="market.logo"></upload>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="market.keywords">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="market.description">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <select class="form-control" v-model="market.floorId" required>
                <option v-for="floor in floors" :value=" floor._id">{{ floor.name }}</option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="market.sortId">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <label class="radio-inline">
                <input type="radio" name="ishidden" value="1" v-model="market.ishidden"> 
              </label>
              <label class="radio-inline">
                <input type="radio" name="ishidden" value="0" v-model="market.ishidden"> 
              </label>
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-10 col-sm-offset-2">
              <button class="btn btn-primary" type="submit"></button>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div slot="modal-footer"></div>
  </modal>

  <!---->
  <modal :show.sync="showFloorModal" title="">
    <div slot="modal-body" class="modal-body">
      <form class="form-horizontal" v-ajax-submit @submit.prevent="saveFloor">
        <div class="form-horizontal">
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="floor.name" required>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <upload id="pick-floor" :img.sync="floor.img"></upload>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="number" class="form-control" v-model="floor.sortId">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <label class="radio-inline">
                <input type="radio" name="floorishidden" value="1" v-model="floor.ishidden"> 
              </label>
              <label class="radio-inline">
                <input type="radio" name="floorishidden" value="0" v-model="floor.ishidden"> 
              </label>
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-10 col-sm-offset-2">
              <button class="btn btn-primary" type="submit"></button>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div slot="modal-footer"></div>
  </modal>
</template>
<script>
var webupload = require('../util/webupload/webuploader.min');

  //
  var marketOpt = {
    sortId: 10,
    ishidden: '1'
  }
  var floorOpt = {
    sortId: 10,
    ishidden: '1'
  }

  module.exports = {
    data: function() {
      return {
        showModal: false,
        showFloorModal: false,


        markets: [],
        market: $.extend({}, marketOpt),
        marketIndex: '',

        floors: [],
        floor: $.extend({}, floorOpt),
        floorIndex: '',

        page: {
          total: null,
          current: 1,
          perPage: 10
        },

        search: {
          floorId: null
        }


      }
    },
    route: {
      data: function(transition) {
        this.getMarket();
        this.getFloor();
      }
    },
    methods: {
      //
      getMarket: function() {
        var self = this;
        var params = $.extend({}, this.page, this.search);
        $.ajax({
          url: domain + '/market',
          data: params,
          type: 'GET'
        })
        .done(function(res) {
          if ('1' == res.status) {
            self.page.total = res.count;
            self.markets = res.data;
          }
        });
      },
      //
      addMarket: function() {
        var self = this;
        this.market = $.extend({}, marketOpt);
        this.showModal = true;
      },
      editMarket: function(index) {
        var self = this;
        self.market = $.extend({}, self.markets[index]);
        self.marketIndex = index;
        this.showModal = true;
      },
      saveMarket: function() {
        var self = this;
        var param = JSON.parse(JSON.stringify(self.market));
        $.ajax({
          url: domain + '/market',
          type: 'post',
          dataType: 'json',
          data: param
        })
        .done(function(res) {
          if (1 == res.status) {
            if(!param._id) {
              param._id = res.data._id;
              self.markets.push(param);
            } else {
              self.markets.$set(self.marketIndex, param);
            }
            self.showModal = false;
          }
        });
        return false;
      },
      delMarket: function(id, index) {
        var self = this;
        if (confirm('')) {
          if (id) {
            $.ajax({
              url: domain + '/market/' + id,
              type: 'delete',
            })
            .done(function(res) {
              if(1 == res.status) {
                self.markets.splice(index, 1);
              }
            })
          }
        }
      },


      //
      getFloor: function() {
        var self = this;
        $.ajax({
          url: domain + '/market/floor',
          type: 'GET'
        })
        .done(function(res) {
          if ('1' == res.status && res.data) {
            self.floors = res.data;
          }
        });
      },
      addFloor: function() {
        var self = this;
        this.floor = $.extend({}, floorOpt);
        this.showFloorModal = true;
      },

      editFloor: function(index) {
        var self = this;
        self.floor = $.extend({}, self.floors[index]);
        self.floorIndex = index;
        this.showFloorModal = true;
      },
      saveFloor: function() {
        var self = this;
        var param = JSON.parse(JSON.stringify(self.floor));
        $.ajax({
          url: domain + '/market/floor',
          type: 'post',
          dataType: 'json',
          data: param
        })
        .done(function(res) {
          if (1 == res.status) {
            if(!param._id) {
              param._id = res.data._id;
              self.floors.push(param);
            } else {
              self.floors.$set(self.floorIndex, param);
            }
            self.showFloorModal = false;
          }
        });
        return false;
      },
      delFloor: function(id, index) {
        var self = this;
        if (confirm('')) {
          if (id) {
            $.ajax({
              url: domain + '/market/floor/' + id,
              type: 'delete',
            })
            .done(function(res) {
              if(1 == res.status) {
                self.floors.splice(index, 1);
              }
            })
          }
        }
      },

      //
      pagerCallback: function(page) {
        this.page.current = page;
        this.getMarket();
      },
      floorFilter: function(id) {
        this.search.floorId = id;
        this.page.current = 1;
        this.getMarket();
      }
    },
    components: {
      dropdown: require('../components/dropdown.vue')
    }
  }
</script>
<style>
  .palent{
    background: #fff;
    border:1px solid #ddd;
    padding: 10px;
  }
  .mb10{
    margin-bottom: 10px;
  }
</style>



